<template>
  <div :id="$style.app" :class="isDarkMode ? $style.dark : $style.light">
    <router-view/>
  </div>
</template>

<script>
import localForage from 'localforage'

export default {
  data() {
    return {
      isDarkMode: false,
    }
  },
  watch: {
    '$store.state.darkMode'(val) {
      this.isDarkMode = this.$store.state.darkMode;
    }
  },
  created() {
    if (this.$store.state.darkMode !== undefined) {
      this.isDarkMode = this.$store.state.darkMode
    }
    this.getTheme();
  },
  methods: {
    async getTheme() {
      const isDarkMode = await localForage.getItem('darkMode').then(res => res)
      this.isDarkMode = isDarkMode
      return isDarkMode
    },
  },
}
</script>

<style lang="scss" module>
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1 {
  margin: 0;
}

#app {
  font-family: Arial, sans-serif;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
  transition: background-color 0.2s ease, color 0.2s ease;
}

.dark {
  background-color: #292929;
  transition: background-color 0.2s ease, color 0.2s ease;
  color: white;
}

.light {
  background-color: white;
  transition: background-color 0.2s ease, color 0.2s ease;
  color: #292929;
}
</style>
