<template>
  <div>
    <span :class="$style.bigEmoji">‚è∞ üöÄ</span>
    <h1>orafal.dev - work in progress...</h1>
    <div :class="$style.darkMode">
      <label for="dark_mode">
        <input type="checkbox" id="dark_mode" v-model="darkMode"/>
        Dark mode
      </label>
    </div>
  </div>
</template>

<script>
import localForage from 'localforage'

export default {
  name: 'home',
  watch: {
    darkMode: {
      handler() {
        console.log('darkMode changed!')
        localForage.setItem('darkMode', this.$store.state.darkMode).then((res) => {
          console.log(res)
        })
      },
    },
  },
  mounted() {
    localForage.keys().then(keys => {
      if (keys.indexOf('darkMode') >= 0) {
        localForage.getItem('darkMode').then(res => {
          this.darkMode = res;
        })
      }
    })
  },
  computed: {
    darkMode: {
      get() {
        return this.$store.state.darkMode;
      },
      set(val) {
        this.$store.dispatch('setDarkMode', val);
      }
    }
  }
}
</script>

<style lang="scss" module>
.big-emoji {
  font-size: 48px;
}

.dark-mode {
  margin-top: 20px;
}
</style>
