import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: { darkMode: false },
  mutations: {
    setDarkMode(state, value) {
      state.darkMode = value
    },
  },
  actions: {
    setDarkMode({ commit }, value) {
      commit('setDarkMode', value)
    },
  },
})
